html {
  font-size: 62.5%;
}

body {
  background-color: #fff;
  font-family: "Raleway", sans-serif;
}

img {
  max-width: 100%;
}

a {
  color: #233E59;
  text-decoration: none;
}

ul, li {
  margin: 0;
  list-style: none;
}

.boxShadow {
  box-shadow: 0 0 10px 0 #cccccc;
}

.radius {
  border-radius: 8px;
}

.link {
  display: inline-block;
  padding: 5px;
}

.content {
  min-height: calc(100vh - 94px);
  padding: 0 0 40px;
}
.content .limit {
  height: calc(100vh - 94px);
  width: 100vw;
  overflow: scroll;
}

.limit-m {
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
}

.p-10 {
  padding: 10px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100vw;
  padding: 10px;
  background-color: #fff;
}
.header__inner {
  text-align: right;
}

.content--make {
  max-height: 100vh;
  padding: 50px 0 60px 0;
  overflow: scroll;
}

.frame-content .title {
  font-size: 3vw;
  color: #cccccc;
}
.frame-content .en {
  font-size: 3.5vw;
}
.frame-content .en.text {
  font-size: 1.2rem;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.footer__inner {
  background-color: #fff;
  text-align: center;
}
.footer--select .list {
  display: flex;
  justify-content: end;
}
.footer--select .item {
  max-height: 120px;
  width: 120px;
}

.side {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  height: 100vh;
  width: 230px;
  background-color: #fff;
}
.side .item {
  padding: 0 0 20px;
  transition: box-shadow 0.2s;
}
.side .item.active {
  box-shadow: 0 0 2px 0 #233E59 inset;
}
.side .item:hover {
  cursor: pointer;
  box-shadow: 0 0 2px 0 #233E59 inset;
}
.side .item .caption {
  padding: 0 0 10px;
}
.side .add-box__btn {
  background-color: #233E59;
  color: #fff;
  margin: 0 0 10px;
  cursor: pointer;
}

.open-modal {
  height: 100vh;
  overflow: hidden;
}

.modal {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  display: none;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.16);
  text-align: center;
}
.modal--inner {
  padding: 20px;
}
.modal.view {
  display: block;
}
.modal .iframe {
  height: 90vh;
  width: 100%;
  border: none;
}
.modal .close {
  position: absolute;
  top: 10px;
  right: 20px;
  padding: 10px;
  background-color: #fff;
  font-size: 1.4rem;
}

.box--select {
  height: 1200px;
  width: 1200px;
  margin: 0 0 0 240px;
  padding: 10px;
  background-size: 100%;
}
.box--select .card {
  position: relative;
  z-index: 2;
  height: 60px;
  max-width: 60px;
  width: 100%;
  background-color: #fff;
}
.box--select .card .btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: #fff;
}
.box--select .card .btn--control {
  font-size: 2rem;
}
.box--select .card .btn--control:hover {
  cursor: pointer;
}
.box--select .card .title {
  margin: 0 0 10px;
  border--bottom: 1px solid #cccccc;
  font-size: 1.4rem;
}
.box--select .card .view.info-box {
  display: block;
}
.box--select .card .info-box {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  height: 240px;
  width: 320px;
  padding: 10px;
  background-color: #fff;
}